import {
		ajax,
	} from '@/static/js/base.js'
	import api from '@/static/js/api.js'
export default {
	data() {
		return {
			array: ['小学', '初中', '高中', '大专','本科','研究生','硕士'],
			jobTime:['无经验','1-3年工作经验','3-5年工作经验','5年以上'],
			index: 0,
			jobIndex: 0,
			formNode:{
				hr_mobile: '',
				name: '',
				post_name: '',
				post_address: '',
				post_salary: '',
				address:'',
				post_education:'请选择学历',
				post_experience:'请选择工作经验',
				post_details: '',
				hr_name:'',
				hr_post: '',
				logo: '',
				staff: '',
				financing: ''
			},
		};
	},
	onLoad() {
		console.log(api.IMG_URL)
	},
	methods: {
		uploadImg() {
			const that = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								console.log(item);
								if (item.size > 10000000) {
									// if (item.size > 500) {
									uni.showToast({
										title: '上传图片不能大于5兆',
										icon: 'none',
										duration: 2000
									});
								} else {
									uni.uploadFile({
										url: api.home_upload_img,
										filePath: item.path,
										name: 'image',
										formData:{
											'disk': 'recruit'
										},
										header: {
											// Authorization: this.$parent.globalData.token
										},
										success: res => {
											resolve({
												path: JSON.parse(res.data).data
											});
										}
									});
								}
							});
						})
					)
						.then(e => {
							uni.hideToast();
							console.log(e);
							// let image = that.formNode.resume;
							that.formNode.logo = e[0].path;
							console.log(that.formNode.logo)
						})
						.catch(err => console.log(err));
				}
			});
		},
		jobChange(e){
			this.formNode.post_experience = this.jobTime[e.target.value]
		},
		eduChange(e){
			this.formNode.post_education	 = this.array[e.target.value]
		},
		inputValue(e){
			let formNode = this.formNode;
			let name = e.currentTarget.dataset.name;
			let value = e.detail.value;
			formNode[name] = value;
			this.formNode = formNode;
		},
		submit(){
			console.log(this.formNode)
			if(this.formNode.post_name == ''){
				uni.showToast({
					title: '请填写求职岗位',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.post_salary == ''){
				uni.showToast({
					title: '请填写薪资待遇',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.post_address == ''){
				uni.showToast({
					title: '请填写工作地点',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.post_education == '请选择学历'){
				uni.showToast({
					title: '请填写学历',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.post_experience == '请选择工作经验'){
				uni.showToast({
					title: '请填写工作经验',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.post_details == ''){
				uni.showToast({
					title: '请填写职位详情',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.hr_name == ''){
				uni.showToast({
					title: '请填写姓名',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.hr_mobile == ''){
				uni.showToast({
					title: '请填写联系方式',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.name == ''){
				uni.showToast({
					title: '请填写公司名称',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.hr_post == ''){
				uni.showToast({
					title: '请填写招聘人岗位名称',
					icon: 'none'
				})
				return false
			}
			// if(this.formNode.resume == ''){
			// 	uni.showToast({
			// 		title: '请上传简历图片',
			// 		icon: 'none'
			// 	})
			// 	return false
			// }
			if(this.formNode.financing == ''){
				uni.showToast({
					title: '请填写融资规模',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.staff == ''){
				uni.showToast({
					title: '请填写员工规模',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.address == ''){
				uni.showToast({
					title: '请填写公司地址',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.logo == ''){
				uni.showToast({
					title: '请上传logo',
					icon: 'none'
				})
				return false
			}
			ajax({
				url: api.addRecruit,
				type: "POST",
				data: this.formNode,
			}).then(res => {
				if (res.status_code == "ok") {
					console.log(res)
					uni.showToast({
						title: res.message,
						icon:'none'
					})
					setTimeout( e =>{
						uni.navigateBack({
							delta:1
						})
					},2000)
					// this.info = res.data
				}
			})
		}
	}
};
